import { Component, OnInit, OnDestroy } from '@angular/core';
import { first, map } from 'rxjs/operators';

import { Booking } from 'src/app/services/data/bookings/booking.class';
import { BaseDirective } from 'src/app/shared/base.directive';
import { SpacesService } from 'src/app/services/data/spaces/spaces.service';
import { BookingStateService } from 'src/app/services/data/bookings/booking-state.service';

@Component({
    selector: 'day-view-approvals-view',
    templateUrl: './approvals-view.component.html',
    styleUrls: ['./approvals-view.component.scss'],
})
export class DayViewApprovalsComponent extends BaseDirective implements OnInit, OnDestroy {
    /** List of spaces to check for pending approvals */
    public space_list: string[] = [];
    /** Whether the pending bookings are being loaded */
    public loading: boolean;
    /** Whether an event action is in progress */
    public waiting_on_event: boolean;
    /** Observable for tentative events */
    public events = this._bookings.filtered.pipe(
        map((bookings) => bookings.filter((bkn) => bkn.tentative))
    );

    /** Display value for the active date */
    public get date_display(): string {
        return 'Today';
    }

    constructor(private _spaces: SpacesService, private _bookings: BookingStateService) {
        super();
    }

    public ngOnInit(): void {
        this._spaces.initialised.pipe(first((_) => _)).subscribe(() => {
            this._bookings.startPollingMonth();
        });
    }

    public ngOnDestroy() {
        this._bookings.stopPolling();
    }

    /* istanbul ignore next */
    /**
     * Track by function for ngFor optimisation
     * @param index Index of the item
     * @param item Data associated with the item at the given index
     */
    public trackByFn(index: number, item: Booking) {
        return item.id || index;
    }
}
