import { Component, Input } from '@angular/core';
import { map } from 'rxjs/operators';

import { Space } from 'src/app/services/data/spaces/space.class';
import { BaseDirective } from 'src/app/shared/base.directive';
import { BookingStateService } from 'src/app/services/data/bookings/booking-state.service';

import * as dayjs from 'dayjs';
import { timePeriodsIntersect } from 'src/app/services/data/bookings/booking.utilities';

@Component({
    selector: 'week-view-day-display',
    templateUrl: './day-display.component.html',
    styleUrls: ['./day-display.component.scss'],
})
export class WeekViewDayDisplayComponent extends BaseDirective {
    /** Date to display bookings for */
    @Input() public date: number;
    /** List of spaces to display bookings for */
    @Input() public spaces: Space[];
    /** List of bookings for this date */
    public readonly booking_list = this._bookings.filtered_week.pipe(
        map((bookings) => {
            const date = dayjs(this.date).startOf('d');
            return bookings.filter((booking) => {
                const start = dayjs(booking.date);
                const end = start.add(booking.duration, 'm');
                return (
                    booking.space_list.find((space) =>
                        this.spaces.find((a_space) => a_space.email === space.email)
                    ) &&
                    timePeriodsIntersect(
                        date.valueOf(),
                        date.endOf('d').valueOf(),
                        start.valueOf(),
                        end.valueOf()
                    )
                );
            });
        })
    );

    /** Display string for the set date */
    public get date_display(): string {
        const date = dayjs(this.date);
        return date.format('ddd, DD MMM YYYY');
    }

    /** Whether the date set is today */
    public get is_today(): boolean {
        return dayjs(this.date).isSame(dayjs(), 'd');
    }

    constructor(private _bookings: BookingStateService) {
        super();
    }
}
