import { Component, OnInit, Input, SimpleChanges } from '@angular/core';
import { first } from 'rxjs/operators';

import { BaseDirective } from 'src/app/shared/base.directive';
import { Space } from 'src/app/services/data/spaces/space.class';
import { SpacesService } from 'src/app/services/data/spaces/spaces.service';
import { BookingStateService } from 'src/app/services/data/bookings/booking-state.service';

import * as dayjs from 'dayjs';

@Component({
    selector: 'week-view-timeline',
    templateUrl: './timeline.component.html',
    styleUrls: ['./timeline.component.scss'],
})
export class WeekViewTimelineComponent extends BaseDirective implements OnInit {
    /** Timestamp of the active date */
    @Input() public date: number;
    /** ID of the active level */
    @Input() public level: string;
    /** ID of the room type to filter displayed spaces by */
    @Input() public space_type: string;
    /** Whether weekends are displayed */
    @Input() public weekends: boolean;
    /** Whether space bookings are being loaded */
    @Input() public loading: boolean;
    /** List of dates to display */
    public date_list: number[] = [];
    /** List of spaces to display bookings for */
    public space_list: Space[] = [];

    constructor(
        private _spaces: SpacesService,
        private _bookings: BookingStateService
    ) {
        super();
    }

    public ngOnInit(): void {
        this._spaces.initialised.pipe(first(_ => _)).subscribe(() => {
            this.date_list = this.generateDates(this.date, this.weekends);
            this._bookings.startPollingWeek();
            this.space_list = this._spaces.filter(space => space.zones.includes(this.level));
        });
    }

    public ngOnChanges(changes: SimpleChanges): void {
        /* istanbul ignore else */
        if (changes.date || changes.weekends || changes.level || changes.space_type) {
            this.date_list = this.generateDates(this.date, this.weekends);
            this.space_list = this._spaces.filter(space => space.zones.includes(this.level));
        }
    }

    public ngOnDestroy() {
        this._bookings.stopPolling();
    }

    /**
     * Generate list of dates to display
     * @param date Currently selected date
     * @param weekends Whether weekends should be included
     */
    private generateDates(date: number, weekends: boolean = false): number[] {
        const list: number[] = [];
        let day = dayjs(date).startOf('d');
        let start = day.startOf('w');
        const end = start.endOf('w');
        while (start.isBefore(end, 'm')) {
            if ((start.day() !== 0 && start.day() !== 6) || weekends) {
                list.push(start.valueOf());
            }
            start = start.add(1, 'd');
        }
        return list;
    }
}
